<template>
  <PatchMeta
    :siteName="'tvbarata.com'"
    :title="'¡Año nuevo, TV nueva!'"
    :description="'Las mejores teles de 2021 para ayudarte a olvidar 2020'"
  />

  <CustomNavBar
    title="📺 TVbarata.com"
    :sections="sections"
  />

  <router-view />

  <CustomFooter>
    <a href="#/" class="text-reset"><p class="m-0 p-0">TVbarata.com</p></a>
  </CustomFooter>
</template>

<script lang='ts'>
import { defineComponent, inject } from 'vue'
import CustomNavBar from '@shared/components/CustomNavBar.vue'
import CustomFooter from '@shared/components/CustomFooter.vue'
import PatchMeta from '@shared/components/PatchMeta.vue'
import { ShopItem } from '@shared/components/types/ShopItem'
import 'bootstrap/dist/css/bootstrap.css'

export default defineComponent({
  components: {
    CustomNavBar,
    CustomFooter,
    PatchMeta
  },
  setup () {
    const data: Record<string, ShopItem> = inject('items', {})

    const allBrands = Object.values(data)
      .filter((i) => i.brand)
      .map((i) => i.brand.trim())

    const sections: Record<string, number> = {
      'Ver Todo': Object.keys(data).length
    }
    allBrands.forEach((b) => {
      sections[b] = sections[b] ? sections[b] + 1 : 1
    })

    return {
      sections
    }
  }
})
</script>

