
import { defineComponent, inject } from 'vue'
import CustomNavBar from '@shared/components/CustomNavBar.vue'
import CustomFooter from '@shared/components/CustomFooter.vue'
import PatchMeta from '@shared/components/PatchMeta.vue'
import { ShopItem } from '@shared/components/types/ShopItem'
import 'bootstrap/dist/css/bootstrap.css'

export default defineComponent({
  components: {
    CustomNavBar,
    CustomFooter,
    PatchMeta
  },
  setup () {
    const data: Record<string, ShopItem> = inject('items', {})

    const allBrands = Object.values(data)
      .filter((i) => i.brand)
      .map((i) => i.brand.trim())

    const sections: Record<string, number> = {
      'Ver Todo': Object.keys(data).length
    }
    allBrands.forEach((b) => {
      sections[b] = sections[b] ? sections[b] + 1 : 1
    })

    return {
      sections
    }
  }
})
